body {
  background-color: black;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 24px;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.App {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.map, .sky {
  position: relative;
  width: 100%;
  background-repeat: repeat-x;
  background-image: url("./images/map.png");
  background-size: auto 480px;
}

.map {
  height: 480px;
}

.sky {
  height: 300px;
}

.map .poke-trainer {
  position: absolute;
  height: auto;
  width: 96px;
  bottom: 92px;
  left: 50%;
}

.map .poke-trainer.left {
  -webkit-transform: scaleX(1) translateX(-50%);
      -ms-transform: scaleX(1) translateX(-50%);
          transform: scaleX(1) translateX(-50%);
}

.map .poke-trainer.right {
  -webkit-transform: scaleX(-1) translateX(50%);
      -ms-transform: scaleX(-1) translateX(50%);
          transform: scaleX(-1) translateX(50%);
}

.modal-shade {
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.modal-content {
  width: calc(100% - 1.5em);
  max-width: 480px;
  height: 320px;
  border-radius: .5em;
  overflow: hidden;
}

.modal-content {
  background-color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 1em;
}

.modal-content .sprite {
  height: 45%;
  width: auto;
}

.modal-catch {
  text-align: center;
  padding: 1em 2em;
  background-color: dodgerblue;
  color: white;
}

.modal-run {
  color: dodgerblue;
}

.caught-pokemon {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
}

.caught-pokemon img {
  width: auto;
  height: 100px;
}

.poke-stats {
  padding: .2em;
  position: absolute;
  bottom: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: baseline;
      align-items: baseline;
}

.poke-stats img {
  height: 32px;
}

.poke-stats span {
  display: block;
  color: white;
  font-size: .66em;
}


